.container {
  padding: 40px 120px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  row-gap: 25px;
  width: 100%;
  height: 500px;
}
.container .map {
  width: 100%;
  height: 400px;
}
.container h2 {
  width: 100%;
  text-align: start;
  color: #003680;
  font-size: 2rem;
  text-transform: uppercase;
}

