.container {
  display: flex;
  flex-direction: column;
  height: 450px;
  justify-content: center;
  row-gap: 100px;
  align-content: space-between;
}
.container h3 {
  width: 60%;
  color: white;
  text-shadow: 0px 1px 1px black;
  font-size: 1.8rem;
  line-height: 1.5;
  position: relative;
  text-align: left;
  z-index: 600;
}
.container h3:after {
  display: block;
  content: "";
  width: 100%;
  height: 2px;
  background-color: #e0e0e0;
  position: absolute;
  bottom: -40px;
}
.container p {
  width: 60%;
  color: white;
  font-size: 1.6rem;
  line-height: 1.2;
  text-align: left;
  z-index: 600;
  text-shadow: 0px 1px 1px black;
}

